
.loading-wrapper {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 100vw;
  height: 100vh;
  background-color: #898989;
  font-size: 4em;
  color: white;
  -webkit-transition: height 0.8s;
  transition: height 0.8s;
}
.hidden {
  height: 0;
}
.wrapper {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  border: 10px solid #ff943d;
  -webkit-transition: border-color 0.2s ease-in;
  transition: border-color 0.2s ease-in;
}
.wrapper:hover {
    border: 10px solid #ef6c00;
    -webkit-transition: border-color 0.2s ease-in;
    transition: border-color 0.2s ease-in;
}
canvas {
  cursor: crosshair;
}
.row {
  margin: 10vh 5vw;
}
.center {
  text-align: center;
}

